
<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid text-start">
        <p class="fs-5 m-2">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!videoSession">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else>
          <div class="row g-0">
            <img
              class="col-12 col-md-2 rounded-0 rounded-start"
              :src="API_STORAGE_URL + product.main_image_path"
            />
            <div
              class="
                bg-primary
                col-12 col-md-10
                fs-2
                p-2
                rounded-0 rounded-end
                text-light
              "
            >
              {{ product.name }}
              <div v-if="!videoSession.free_conference_before_start_text">
                <div class="fs-4 mt-3">
                  جلسه
                  {{ videoSession.name }}
                </div>
                <div class="fs-6 mt-3">
                  روز های برگزاری:
                  {{ product.days }} , ساعت برگزاری: {{ product.hour }} , تاریخ
                  شروع:
                  {{ product.start_date }}
                </div>
              </div>
              <div v-else class="fs-4 mt-3" >
                {{ videoSession.free_conference_before_start_text }}
              </div>
            </div>
          </div>

          <div class="mt-3">
            <div class="fs-3 text-center">
              <SessionStatusMessage
                :startDate="videoSession.start_date"
                :startTime="videoSession.start_time"
                :videoSessionType="
                  videoSession.video_session.video_session_type
                "
                :videoLink="videoSession.video_session.video_link"
                :productDetailViedosId="videoSession.id"
                :videoSessionName="videoSession.name"
                currentTime=""
              />
            </div>
          </div>

          <div class="row mt-3">
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">دریافت جزوه</div>
                <div class="card-body d-flex justify-content-start">
                  <div
                    v-for="item in videoSession.files"
                    :key="item.id"
                    class="
                      border
                      d-flex
                      justify-content-between
                      m-1
                      p-2
                      rounded-1
                      fs-6
                    "
                  >
                    <a :href="API_STORAGE_URL + item.file_path" target="_blank">
                      <i class="fas fa-download"></i>
                      {{ item.name }}
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">ارسال تکلیف</div>
                <div class="card-body">
                  <input class="form-control" type="file" />
                  <a class="btn btn-sm border rounded mt-1"> ارسال </a>
                </div>
              </div>
            </div>
            <div class="col">
              <div class="card">
                <div class="card-header fs-4">آزمون کلاسی</div>
                <div class="card-body">
                  <a class="btn w-100 shadow border rounded my-3">
                    انجام آزمون کلاسی
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div class="row g-0 mt-3">
            <div class="col-12 col-md-6">
              <div class="card">
                <div class="card-header fs-5 text-center">
                  کلیه جلسات این محصول
                </div>
              </div>
            </div>

            <div class="col-12 col-md-6">
              <div class="card m-0 ms-4">
                <div class="card-header fs-5 text-center">ارسال نظر</div>
                <div class="card-body">
                  <textarea
                    v-model="productComment"
                    class="form-control"
                    rows="10"
                  ></textarea>
                  <button class="btn btn-primary mt-2" @click="addComment">
                    ثبت نظر
                  </button>
                  <span v-show="commentSpin">
                    <i class="fas fa-spinner fa-spin"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import utils from "../libs/utils";
import SessionStatusMessage from "./SessionStatusMessage";

export default {
  name: "AdminVideoSession",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.videoSessionId = this.$route.params.videoSessionId;
    this.productId = this.$route.params.productId;
    this.getProduct();
    this.getProductVideoSessionInfo();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
    SessionStatusMessage,
  },
  data() {
    return {
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
      product: null,
      videoSessionList: null,
      videoSession: null,
      productComment: null,
      commentSpin: false,
      videoSessionId: null,
    };
  },
  methods: {
    getProduct() {
      this.arefApi
        .getProduct(this.productId)
        .then((resp) => {
          this.product = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getProductVideoSessionInfo() {
      this.arefApi
        .getProductVideoSessionInfo(this.videoSessionId)
        .then((resp) => {
          this.videoSession = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    addComment(event) {
      if (!this.productComment) {
        this.$swal.fire({
          title: "!خطا",
          text: "متن نظر را وارد کنید",
          icon: "error",
          confirmButtonText: "متوجه شدم",
        });
        return;
      }
      this.commentSpin = true;
      event.target.disabled = true;
      const commentData = {
        products_id: this.product.id,
        comment: this.productComment,
      };
      this.arefApi
        .addProductComment(commentData)
        .then(() => {
          this.productComment = null;
          this.utils.showSuccess(
            this,
            "ثبت با موفقیت انجام شد ، پس از تایید مدیریت منتشر خواهد شد"
          );
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          event.target.disabled = false;
          this.commentSpin = false;
        });
    },
  },
  mounted() {},
  watch: {
    $route(to, from) {
      if (
        to !== from &&
        to.name === "AdminVideoSession" &&
        from.name === "AdminVideoSession"
      ) {
        this.$router.push({ path: to.path });
        this.$router.go();
      }
    },
  },
};
</script>

